
.black-nav{
  display:flex;
  background-color: #D6E8FF;
  width: 100%;
  color: white;
  padding-left: 20px;
}

div{
  box-sizing: border-box;
}

.list{
  padding-left: 20px;
  text-align: left;
  border-bottom: 1px solid lightgray;
}

.modal{
  margin-top: 20px;
  padding: 20px;
  background-color: #eee;
  text-align: left;
}

.nav-area{
  width: 100%;
  height: 70px;
  line-height: 70px;
  background-color: #D6E8FF;
  color: white;
}
.nav-area ul{
  list-style:none;
  width: 100%;
  text-align: center;
}
.nav-area ul li{
  width: 25%;
  float: left;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
